import { create } from 'zustand';
import { asSystemId, asBusinessId, type SystemId, type BusinessId } from '@/lib/id-types';
import type { StorageLocation } from './storage-location-types';

// API sync helper
async function syncToAPI(action: 'create' | 'update' | 'delete', data: Partial<StorageLocation>): Promise<boolean> {
  try {
    const endpoint = action === 'create' ? '/api/stock-locations' : `/api/stock-locations/${data.systemId}`;
    const method = action === 'create' ? 'POST' : action === 'update' ? 'PATCH' : 'DELETE';
    const response = await fetch(endpoint, {
      method,
      headers: { 'Content-Type': 'application/json' },
      body: action !== 'delete' ? JSON.stringify(data) : undefined,
    });
    return response.ok;
  } catch (error) {
    console.error(`[Storage Location API] ${action} error:`, error);
    return false;
  }
}

interface StorageLocationStore {
  data: StorageLocation[];
  initialized: boolean;
  add: (location: Omit<StorageLocation, 'systemId' | 'createdAt' | 'updatedAt'>) => void;
  update: (systemId: SystemId, location: Partial<StorageLocation>) => void;
  remove: (systemId: SystemId) => void;
  findById: (id: BusinessId) => StorageLocation | undefined;
  findBySystemId: (systemId: SystemId) => StorageLocation | undefined;
  getActive: () => StorageLocation[];
  loadFromAPI: () => Promise<void>;
}

const rawData = [
  {
    systemId: crypto.randomUUID(),
    id: 'KHO-A',
    name: 'Kho A',
    description: 'Kho ch√≠nh',
    isDefault: true,
    isActive: true,
    isDeleted: false,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
  },
  {
    systemId: crypto.randomUUID(),
    id: 'KHO-B',
    name: 'Kho B',
    description: 'Kho ph·ª•',
    isActive: true,
    isDeleted: false,
    createdAt: new Date().toISOString(),
    updatedAt: new Date().toISOString(),
  },
] as const;

const initialData: StorageLocation[] = rawData.map((item) => ({
  ...item,
  systemId: asSystemId(item.systemId),
  id: asBusinessId(item.id),
}));

export const useStorageLocationStore = create<StorageLocationStore>()(
  (set, get) => ({
    data: initialData,
    initialized: false,

    add: (location) => {
      const newLocation: StorageLocation = {
        ...location,
        systemId: asSystemId(crypto.randomUUID()),
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
      };
      set((state) => ({ data: [...state.data, newLocation] }));
      syncToAPI('create', newLocation).catch(console.error);
    },

    update: (systemId, updates) => {
      set((state) => ({
        data: state.data.map((loc) =>
          loc.systemId === systemId
            ? { ...loc, ...updates, updatedAt: new Date().toISOString() }
            : loc
        ),
      }));
      const updated = get().data.find(l => l.systemId === systemId);
      if (updated) syncToAPI('update', updated).catch(console.error);
    },

    remove: (systemId) => {
      set((state) => ({
        data: state.data.map((loc) =>
          loc.systemId === systemId
            ? { ...loc, isDeleted: true, updatedAt: new Date().toISOString() }
            : loc
        ),
      }));
      syncToAPI('delete', { systemId }).catch(console.error);
    },

    findById: (id) => {
      return get().data.find((loc) => loc.id === id && !loc.isDeleted);
    },

    findBySystemId: (systemId) => {
      return get().data.find((loc) => loc.systemId === systemId && !loc.isDeleted);
    },

    getActive: () => {
      return get().data.filter((loc) => !loc.isDeleted && loc.isActive);
    },
    
    loadFromAPI: async () => {
      if (get().initialized) return;
      try {
        // NOTE: Settings data is typically small, but using limit for consistency
        const response = await fetch('/api/stock-locations?limit=50');
        if (response.ok) {
          const json = await response.json();
          const data = json.data || [];
          if (data.length > 0) {
            set({ data: data.map((item: any) => ({
              ...item,
              systemId: asSystemId(item.systemId),
              id: asBusinessId(item.id),
            })), initialized: true });
          } else {
            set({ initialized: true });
          }
        }
      } catch (error) {
        console.error('[Storage Location Store] loadFromAPI error:', error);
      }
    },
  })
);
