import { create } from 'zustand';
import type { ProductSlaSettings } from './types';

interface SlaSettingsState {
  settings: ProductSlaSettings;
  initialized: boolean;
  update: (updates: Partial<ProductSlaSettings>) => void;
  reset: () => void;
  loadFromAPI: () => Promise<void>;
}

const defaultSettings: ProductSlaSettings = {
  // Ng∆∞·ª°ng m·∫∑c ƒë·ªãnh
  defaultReorderLevel: 10,
  defaultSafetyStock: 5,
  defaultMaxStock: 100,
  
  // C·∫£nh b√°o h√†ng t·ªìn l√¢u
  deadStockDays: 90,
  slowMovingDays: 30,
  
  // Notifications
  enableEmailAlerts: false,
  alertEmailRecipients: [],
  alertFrequency: 'daily',
  
  // Dashboard
  showOnDashboard: true,
  dashboardAlertTypes: ['out_of_stock', 'low_stock', 'below_safety'],
};

// API sync helper
async function syncToAPI(settings: ProductSlaSettings): Promise<boolean> {
  try {
    const response = await fetch('/api/settings/product-sla', {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(settings),
    });
    return response.ok;
  } catch (error) {
    console.error('[SLA Settings API] sync error:', error);
    return false;
  }
}

export const useSlaSettingsStore = create<SlaSettingsState>()(
  (set, get) => ({
    settings: defaultSettings,
    initialized: false,
    
    update: (updates) => {
      const newSettings = { ...get().settings, ...updates };
      set({ settings: newSettings });
      syncToAPI(newSettings).catch(console.error);
    },
    
    reset: () => {
      set({ settings: defaultSettings });
      syncToAPI(defaultSettings).catch(console.error);
    },
    
    loadFromAPI: async () => {
      if (get().initialized) return;
      try {
        const response = await fetch('/api/settings/product-sla');
        if (response.ok) {
          const json = await response.json();
          const data = json.data || json;
          if (data && Object.keys(data).length > 0) {
            set({ settings: { ...defaultSettings, ...data }, initialized: true });
          } else {
            set({ initialized: true });
          }
        }
      } catch (error) {
        console.error('[SLA Settings Store] loadFromAPI error:', error);
      }
    },
  })
);
