import { create } from 'zustand';
import { nanoid } from 'nanoid';
import { asSystemId, type SystemId } from '@/lib/id-types';
import type { Importer } from './types';

// API sync helper
async function syncToAPI(action: 'create' | 'update' | 'delete', data: Partial<Importer>): Promise<boolean> {
  try {
    const endpoint = action === 'create' ? '/api/settings/importers' : `/api/settings/importers/${data.systemId}`;
    const method = action === 'create' ? 'POST' : action === 'update' ? 'PATCH' : 'DELETE';
    const response = await fetch(endpoint, {
      method,
      headers: { 'Content-Type': 'application/json' },
      body: action !== 'delete' ? JSON.stringify(data) : undefined,
    });
    return response.ok;
  } catch (error) {
    console.error(`[Importer API] ${action} error:`, error);
    return false;
  }
}

interface ImporterState {
  data: Importer[];
  initialized: boolean;
  add: (importer: Omit<Importer, 'systemId' | 'createdAt' | 'updatedAt'>) => void;
  update: (systemId: SystemId, importer: Partial<Importer>) => void;
  remove: (systemId: SystemId) => void;
  setDefault: (systemId: SystemId) => void;
  getActive: () => Importer[];
  getDefault: () => Importer | undefined;
  findById: (systemId: SystemId) => Importer | undefined;
  loadFromAPI: () => Promise<void>;
}

export const useImporterStore = create<ImporterState>()(
  (set, get) => ({
    data: [],
    initialized: false,
    
    add: (importer) => {
      // N·∫øu ƒë√¢y l√† item ƒë·∫ßu ti√™n ho·∫∑c isDefault = true, set l√†m default
      const isFirstItem = get().data.filter(d => !d.isDeleted).length === 0;
      const shouldBeDefault = isFirstItem || importer.isDefault;
      
      // N·∫øu item m·ªõi l√† default, b·ªè default c·ªßa c√°c item kh√°c
      const updatedData = shouldBeDefault
        ? get().data.map(item => ({ ...item, isDefault: false }))
        : get().data;
      
      const newItem: Importer = {
        ...importer,
        systemId: asSystemId(nanoid()),
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
        isActive: importer.isActive ?? true,
        isDefault: shouldBeDefault,
        isDeleted: false,
      };
      
      set({ data: [...updatedData, newItem] });
      syncToAPI('create', newItem).catch(console.error);
    },
    
    update: (systemId, importer) => {
      set((state) => ({
        data: state.data.map((item) =>
          item.systemId === systemId
            ? { ...item, ...importer, updatedAt: new Date().toISOString() }
            : item
        ),
      }));
      const updated = get().data.find(i => i.systemId === systemId);
      if (updated) syncToAPI('update', updated).catch(console.error);
    },
    
    remove: (systemId) => {
      set((state) => ({
        data: state.data.map((item) =>
          item.systemId === systemId
            ? { ...item, isDeleted: true, updatedAt: new Date().toISOString() }
            : item
        ),
      }));
      syncToAPI('delete', { systemId }).catch(console.error);
    },
    
    setDefault: (systemId) => {
      set((state) => ({
        data: state.data.map((item) => ({
          ...item,
          isDefault: item.systemId === systemId,
          updatedAt: item.systemId === systemId ? new Date().toISOString() : item.updatedAt,
        })),
      }));
      const updated = get().data.find(i => i.systemId === systemId);
      if (updated) syncToAPI('update', updated).catch(console.error);
    },
    
    getActive: () => get().data.filter((item) => !item.isDeleted && item.isActive),
    
    getDefault: () => get().data.find((item) => !item.isDeleted && item.isDefault),
    
    findById: (systemId) => get().data.find((item) => item.systemId === systemId),
    
    loadFromAPI: async () => {
      if (get().initialized) return;
      try {
        // NOTE: Settings data is typically small, but using limit for consistency
        const response = await fetch('/api/settings/importers?limit=100');
        if (response.ok) {
          const json = await response.json();
          const data = json.data || [];
          set({ data: data.length > 0 ? data : [], initialized: true });
        } else {
          set({ initialized: true });
        }
      } catch (error) {
        console.error('[Importer Store] loadFromAPI error:', error);
      }
    },
  })
);
