import * as React from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import {
  Dialog,
  DialogContent,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '../../../components/ui/dialog';
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '../../../components/ui/form';
import { Input } from '../../../components/ui/input';
import { Textarea } from '../../../components/ui/textarea';
import { Button } from '../../../components/ui/button';
import { Checkbox } from '../../../components/ui/checkbox';
import type { StorageLocation } from './storage-location-types';

// StorageLocation Form Schema
const storageLocationSchema = z.object({
  id: z.string().min(1, 'M√£ ƒëi·ªÉm l∆∞u kho l√† b·∫Øt bu·ªôc'),
  name: z.string().min(1, 'T√™n ƒëi·ªÉm l∆∞u kho l√† b·∫Øt bu·ªôc'),
  description: z.string().optional(),
  isActive: z.boolean().optional(),
});

export type StorageLocationFormValues = z.infer<typeof storageLocationSchema>;

interface StorageLocationFormDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  initialData: StorageLocation | null;
  onSubmit: (data: StorageLocationFormValues) => void;
  existingIds: string[];
}

export function StorageLocationFormDialog({
  open,
  onOpenChange,
  initialData,
  onSubmit,
  existingIds,
}: StorageLocationFormDialogProps) {
  const form = useForm<StorageLocationFormValues>({
    resolver: zodResolver(storageLocationSchema),
    defaultValues: {
      id: initialData?.id || '',
      name: initialData?.name || '',
      description: initialData?.description || '',
      isActive: initialData?.isActive ?? true,
    },
  });

  React.useEffect(() => {
    if (open) {
      form.reset({
        id: initialData?.id || '',
        name: initialData?.name || '',
        description: initialData?.description || '',
        isActive: initialData?.isActive ?? true,
      });
    }
  }, [open, initialData, form]);

  // Validate unique ID
  React.useEffect(() => {
    const subscription = form.watch((value, { name }) => {
      if (name === 'id' && value.id) {
        const isDuplicate = existingIds.includes(value.id) && value.id !== initialData?.id;
        if (isDuplicate) {
          form.setError('id', { type: 'manual', message: 'M√£ ƒëi·ªÉm l∆∞u kho ƒë√£ t·ªìn t·∫°i' });
        } else {
          form.clearErrors('id');
        }
      }
    });
    return () => subscription.unsubscribe();
  }, [form, existingIds, initialData]);

  const handleSubmit = (data: StorageLocationFormValues) => {
    onSubmit(data);
    form.reset();
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>
            {initialData ? 'Ch·ªânh s·ª≠a ƒëi·ªÉm l∆∞u kho' : 'Th√™m ƒëi·ªÉm l∆∞u kho m·ªõi'}
          </DialogTitle>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(handleSubmit)} className="space-y-4">
            <FormField
              control={form.control}
              name="id"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>M√£ ƒëi·ªÉm l∆∞u kho <span className="text-destructive">*</span></FormLabel>
                  <FormControl>
                    <Input {...field} value={field.value ?? ''} placeholder="VD: KHO-A" className="h-9" />
                  </FormControl>
                  <FormDescription>
                    Ch·ªâ ch·ª©a ch·ªØ in hoa v√† s·ªë, t·ªëi ƒëa 20 k√Ω t·ª±
                  </FormDescription>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="name"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>T√™n ƒëi·ªÉm l∆∞u kho <span className="text-destructive">*</span></FormLabel>
                  <FormControl>
                    <Input {...field} value={field.value ?? ''} placeholder="VD: Kho A" className="h-9" />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="description"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>M√¥ t·∫£</FormLabel>
                  <FormControl>
                    <Textarea {...field} value={field.value ?? ''} placeholder="M√¥ t·∫£ chi ti·∫øt..." rows={3} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />

            <FormField
              control={form.control}
              name="isActive"
              render={({ field }) => (
                <FormItem className="flex items-center space-x-2 space-y-0">
                  <FormControl>
                    <Checkbox
                      checked={field.value}
                      onCheckedChange={field.onChange}
                    />
                  </FormControl>
                  <FormLabel className="!mt-0 cursor-pointer">
                    K√≠ch ho·∫°t
                  </FormLabel>
                </FormItem>
              )}
            />

            <DialogFooter>
              <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>
                Tho√°t
              </Button>
              <Button type="submit">
                X√°c nh·∫≠n
              </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
