import { create } from 'zustand';

export interface WarrantySettings {
  /** Th·ªùi h·∫°n b·∫£o h√†nh m·∫∑c ƒë·ªãnh (th√°ng) - √°p d·ª•ng cho t·∫•t c·∫£ SP n·∫øu kh√¥ng c·∫•u h√¨nh ri√™ng */
  defaultWarrantyMonths: number;
}

interface WarrantySettingsState {
  settings: WarrantySettings;
  initialized: boolean;
  update: (updates: Partial<WarrantySettings>) => void;
  reset: () => void;
  loadFromAPI: () => Promise<void>;
}

const defaultSettings: WarrantySettings = {
  defaultWarrantyMonths: 12,
};

// API sync helper
async function syncToAPI(settings: WarrantySettings): Promise<boolean> {
  try {
    const response = await fetch('/api/settings/warranty', {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(settings),
    });
    return response.ok;
  } catch (error) {
    console.error('[Warranty Settings API] sync error:', error);
    return false;
  }
}

export const useWarrantySettingsStore = create<WarrantySettingsState>()(
  (set, get) => ({
    settings: defaultSettings,
    initialized: false,
    
    update: (updates) => {
      const newSettings = { ...get().settings, ...updates };
      set({ settings: newSettings });
      syncToAPI(newSettings).catch(console.error);
    },
    
    reset: () => {
      set({ settings: defaultSettings });
      syncToAPI(defaultSettings).catch(console.error);
    },
    
    loadFromAPI: async () => {
      if (get().initialized) return;
      try {
        const response = await fetch('/api/settings/warranty');
        if (response.ok) {
          const json = await response.json();
          const data = json.data || json;
          if (data && Object.keys(data).length > 0) {
            set({ settings: { ...defaultSettings, ...data }, initialized: true });
          } else {
            set({ initialized: true });
          }
        }
      } catch (error) {
        console.error('[Warranty Settings Store] loadFromAPI error:', error);
      }
    },
  })
);

/** Hook ƒë·ªÉ l·∫•y th·ªùi h·∫°n b·∫£o h√†nh m·∫∑c ƒë·ªãnh */
export function useDefaultWarrantyMonths() {
  return useWarrantySettingsStore((state) => state.settings.defaultWarrantyMonths);
}
